.btn-outline-secondary:active:focus,
.btn-outline-secondary:focus {
  box-shadow: none;
}

.btn-outline-secondary,
.btn-outline-secondary:active,
.btn-outline-secondary:hover {
  background-color: unset;
  color: #111;
  border-color: #111;
}
.btn-danger:hover,
.btn-danger:active,
.btn-danger:focus,
.btn-danger:active:focus {
  border-color: unset;
  background-color: #dc3545;
  box-shadow: none;
}

.btn-search-section {
  display: flex;
  justify-content: space-between;
}

.btn-search-section .btn {
  padding: 0.35rem 0.75rem;
}
.has-search {
  width: 100%;
  max-width: 500px;
  /* align-self: end; */
}
.has-search .form-control {
  padding-left: 2.375rem;
  background: rgb(248, 246, 246);
  padding-top: 8px;
  padding-bottom: 8px;
  border: none;
}
.form-control:focus {
  box-shadow: none;
}
.has-search .form-control-feedback {
  position: absolute;
  z-index: 2;
  display: block;
  width: 2.375rem;
  height: 2.375rem;
  line-height: 2.375rem;
  text-align: center;
  pointer-events: none;
  color: #aaa;
}

.filter .btn-outline-secondary {
  color: #dc3545;
  border: 1px solid #e2e2e2;
}

.add-edit-modal input::-webkit-outer-spin-button,
.add-edit-modal input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
.add-edit-modal input[type='number'] {
  -moz-appearance: textfield;
}

.header_nav,
.header_nav:hover {
  text-decoration: none;
  font-size: 24px;
  color: #000;
  display: inline-block;
  width: auto;
}
.header_nav i {
  margin-right: 15px;
  color: #d4d1d1;
}
.filter_btn {
  margin: 0px 8px;
  font-size: 14px;
}
.upload_btn {
  min-width: 150px;
  height: 37px;
  margin-right: 15px;
}
.filter_btn span {
  margin-right: 15px;
}
.upload_container {
  border-radius: 4px;
}
.uploader_header {
  padding: 15px 30px;
  border-bottom: 1px solid #9ea1a2;
  font-size: 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.upload_box {
  width: 574px;
  height: 228px;
  border-radius: 5px;
  border: dashed 2px rgba(0, 0, 0, 0.2);
  margin: 20px 0;
  padding: 20px;
  text-align: center;
}
.disclaimer {
  color: #12284c;
  font-size: 14px;
  text-align: center;
  margin: 0;
}
.disclaimer.error {
  color: red;
}
.sucess i {padding-left: 10px}
.sucess {
  color: #14ae5c;
  background: #ecf8ec;
  padding: 3px 8px;
  display: inline-block;
  border-radius: 4px;
}
.fail {
  color: #ed193b;
  background: #fae9ec;
  padding: 3px 8px;
  display: inline-block;
  border-radius: 4px;
}
input[type='file'] {
  display: none;
}

#drag-file-element {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 1rem;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
}
